<template>
  <div class="card">
    <div class="card-header">
      <h4 class="title">Profile</h4>
    </div>
    <div class="card-body">
      <form>
        <div class="row">
          <div class="col-md-12">
            <fg-input
              type="text"
              label="Username"
              placeholder="Username"
              v-model="user.username"
            >
            </fg-input>
            <fg-input
              type="text"
              label="Full Name"
              placeholder="Full Name"
              v-model="user.fullname"
            >
            </fg-input>
            <fg-input
              type="email"
              label="Email"
              placeholder="Email"
              v-model="user.email"
            >
            </fg-input>

            <fg-input
              type="phone"
              label="Phone"
              placeholder="Phone Number"
              v-model="user.phone"
            >
            </fg-input>

            <fg-input
              type="text"
              label="Role"
              :disabled="true"
              placeholder="Role"
              v-model="user.role"
            >
            </fg-input>
          </div>
        </div>

        <div class="text-center">
          <button
            type="submit"
            class="btn btn-info btn-fill btn-wd"
            @click.prevent="updateProfile"
          >
            Update Profile
          </button>
        </div>
        <div class="clearfix"></div>
      </form>
    </div>
  </div>
</template>
<script>
import decoder from 'jwt-decode';

export default {
  data() {
    return {
      user: {
        username: "",
        email: "",
        fullname: "",
        phone: "0123456911",
        apartment: "Topaz Home",
        role: "System Admin",
        status: true
        // company: 'Paper Dashboard',
        // username: 'michael23',
        // email: '',
        // lastName: 'Faker',
        // address: 'Melbourne, Australia',
        // city: 'melbourne',
        // postalCode: '',
        // aboutMe: `Oh so, your weak rhyme. You doubt I'll bother, reading into it.I'll probably won't, left to my own devicesBut that's the difference in our opinions.`
      }
    };
  },
  created() {
    let params = decoder(localStorage.getItem("token"));
    console.log(params);
    this.user.username = params.username;
    this.user.email = params.email;
    this.user.fullname = params.fullname;
  },
  methods: {
    updateProfile() {
      alert("Your data: " + JSON.stringify(this.user));
    }
  }
};
</script>
